<template>
    <div id="birj-main">
        <header id="header">
            <div id="user-info">
                <h4 id="name">{{user.name}}</h4>
                <div id="balance">
                    <p id="usd-balance">USD: {{user.balance.usd}}</p>
                    <p id="btc-balance">BTC: {{user.balance.btc.toFixed(10)}}</p>
                </div>
            </div>
            <div id="last-trade">
                <h4>Last order: {{user.last}}</h4>
            </div>
        </header>
        <div id="birj-body-main">
            <div id="birj-body-inner">
                <div class="col" id="last-trades">
                    <ul>
                        <h3>My orders</h3>
                        <li v-for="(order, index) in orders">
                            Price: {{order.price}} {{order.priceValue}} <br> Summ: {{order.summ}} {{order.summValue}} <br>
                            <i>{{order.date.getHours()}}:{{order.date.getMinutes()}}:{{order.date.getSeconds()}}</i>
                            <button v-on:click="deleteOrder(index)">X</button>
                        </li>
                    </ul>
                </div>
                <div class="col" id="buy-form">
                    <form @submit.prevent="order()" action="#">
                        <p>Select value: </p>
                        <label for="usd">USD</label><input v-bind:checked="choose(picked)" v-model="picked" type="radio" value="usd" name="choose" id="usd">
                        <label for="btc">BTC</label><input v-bind:checked="choose(picked)" v-model="picked" type="radio" value="btc" name="choose" id="btc"> <br>
                        <!-- <input v-bind:checked="unselected()" type="radio" name="choose" id=""> -->
                        <p>Summ: </p><input v-model="convert.to" id="summ" type="number" step="0.0000000001"><br>
                        <p>Price: </p><div id="price">{{convert.price.toFixed(7)}} {{convert.value}}</div><br>
                        <button>Order</button>
                        <div v-if="user.error" id="alert">
                            {{user.errorText}}
                        </div>                        
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import store from '../store/index.js'
    export default {
        name: "birj",
        data() {
            return {
                user: {
                    name: 'Jan Kowalski',
                    balance: {
                        usd: 2222200,
                        btc: 3
                    },
                    last: null,
                    error: false,
                    errorText: '' ,
                    red: 'green',
                },
                picked: 'usd',
                convert: {
                    value: '',
                    price: 0,
                    to: 0
                }
            }
        },
        computed: {
            orders() {
              return this.$store.state.orders;
            }
        },
        methods: {
            choose(picked) {
                if(picked == "usd"){
                    this.convert.value = "BTC";
                    this.convert.price = (this.convert.to / 11558) * 1.05;
                } else if (picked == 'btc') {
                    this.convert.value = "USD"
                    this.convert.price = (this.convert.to * 11558) * 1.05;
                }
            },
            order() {
                this.convert.to *= 1;
                let curr = parseFloat(this.convert.to);
                if(curr) {
                    if(this.convert.value == "BTC") {
                    if(this.user.balance.btc >= this.convert.price) {
                        this.user.balance.btc -= this.convert.price;
                        this.user.balance.usd += curr;
                        this.$store.state.orders.push({
                            date: new Date(),
                            summ: curr,
                            price: this.convert.price,
                            summValue: 'USD',
                            priceValue: "BTC"
                        });
                        this.user.last = "+" + curr + " USD";
                        this.user.error = false;
                    } else {
                        this.user.error = true;
                        this.user.errorText = "You dont have enought money!";
                        }
                } else if(this.convert.value == "USD") {
                    if(this.user.balance.usd >= this.convert.price) {
                        this.user.balance.usd -= this.convert.price;
                        this.user.balance.btc += curr;
                        this.$store.state.orders.push({
                            date: new Date(),
                            summ: curr,
                            price: this.convert.price,
                            summValue: 'BTC',
                            priceValue: "USD"
                        });
                        this.user.last = "+" + curr + " BTC";
                        this.user.error = false;
                    } else {
                        this.user.error = true;
                        this.user.errorText = "You dont have enought money!";
                        }
                } else {
                    this.user.error = true;
                    this.user.errorText = "Please, choose an amount!";
                }
                } else {
                    this.user.error = true;
                    this.user.errorText = "Please enter a summ!";
                }
                
            },
            deleteOrder(index) {
                if(confirm("Are you sure to delete this order?")) {
                    if(this.orders[index].priceValue == "USD") {
                        this.user.balance.usd += this.orders[index].price;
                        this.user.balance.btc -= this.orders[index].summ;
                        this.user.last = "-" + this.orders[index].price + " USD";
                    }
                    else if(this.orders[index].priceValue == "BTC") {
                        this.user.balance.btc += this.orders[index].price;
                        this.user.balance.usd -= this.orders[index].summ;
                        this.user.last = "-" + this.orders[index].price + " BTC";
                    }
                    this.orders.splice(index, 1);
                }
            }
        },
        created() {
        }
    }
</script>

<style lang="">
body {
    background: #e7e7e7;
    margin: 0;
    padding: 0;
}
    #header p,
    #header h4  {
        margin: 0;
        display: inline-block;
    }

    #user-info p {
        font-size: 12px;
    }
    #user-info {
        display: inline-block;
        line-height: 35px;
    }
    #last-trade {
        float: right;
        margin-right: 40px;
        line-height: 70px;
        display: inline-block;
    }
    #last-trade h4 {
        margin: 0;
    }
    #header {
        background: #c8c8c8;
        width: 100%;
        padding: 0 20px;
        height: 70px;
        border-bottom: 1px #999 solid;
    }
    #birj-body-main {
        padding: 10px;
    }
    #birj-body-inner {
        width: 100%;
        display: flex;
    }
    .col {
        margin: 0 10px;
        width: 50%;
        background: #fff;
        border-radius: 6px;
    }
    #buy-form {
        height: 100px;
        line-height: 25px;
        padding: 10px;
    }
    #price {display: inline-block;}
    form p {
        width: 100px;
        display: inline-block;
        margin: 0;
    }
    #last-trades ul {
        margin: 10px 0;
        padding: 0 10px;
        max-height: 500px;
        overflow-y: scroll;
    }
    #last-trades ul li {
        position: relative;
        background: #e8e8e8;
        border: 1px #999 solid;
        margin: 5px 0;
        list-style: none;
        padding: 5px 5px 17px;
    }
    #last-trades ul li i {
        float: right;
        font-size: 12px;
        font-weight: bold;
    }
    #last-trades ul li button {
        position: absolute;
        top: 5px;
        right: 5px;
        border: none;
        background: transparent;
        font-weight: bold;
        color: #999;
    }
    #last-trades ul li button:hover {
        color: #666;
    }
</style>


